<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-row :gutter="20">
      <el-col :span="9">
        <el-card class="userreport">
          <div id="user"></div>
        </el-card>
      </el-col>
      <el-col :span="15">
        <el-card>
          <div class="ratio">
            <div id="detail"></div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>
<script>
// import { finddata } from "@/common/crod/index";
import echarts from "echarts";
// 
import "echarts/src/chart/pie";

// domecharts

export default {
  data() {
    return {
      // 
      Bj: [],
      // 
      Hn: [],
      // 
      Sh: [],
      // 
      Gd: [],
      // 
      Other: [],
      // 
      Bdetail: [0, 0, 0, 0],
      // 
      Hdetail: [0, 0, 0, 0],
      // 
      Sdetail: [0, 0, 0, 0],
      // 
      Gdetail: [0, 0, 0, 0],
      // 
      Odetail: [0, 0, 0, 0]
    };
  },
  created() {
    
  },
  methods: {
    // 
    async getusers() {
      const { data: res } = await this.$http.get("/home/users", {
        params: { type: "total" }
      });
      // 
      if (res.code !== 1) return this.$message.error(res.msg);
      // 
      const reg = /^((?![|||]).)*$/;

      // 
      this.Bj = res.data.filter(item => {
        return item.region === "";
      });
      this.Hn = res.data.filter(item => {
        return item.region === "";
      });
      this.Sh = res.data.filter(item => {
        return item.region === "";
      });
      this.Gd = res.data.filter(item => {
        return item.region === "";
      });
      this.Other = res.data.filter(item => {
        return reg.test(item.region);
      });
      // 
      // 
      this.Bj.forEach(item => {
        let bir = new Date(item.birthday);
        let year = bir.getFullYear();
        let nowtime = new Date();
        let nowyear = nowtime.getFullYear();
        if (item.gender === 1) {
          this.Bdetail[0]++;
        }
        if (item.gender === 0) {
          this.Bdetail[1]++;
        }
        if (parseInt(nowyear) - parseInt(year) < 18) {
          this.Bdetail[2]++;
        }
        if (parseInt(nowyear) - parseInt(year) > 18) {
          this.Bdetail[3]++;
        }
      });
      // 
      this.Hn.forEach(item => {
        let bir = new Date(item.birthday);
        let year = bir.getFullYear();
        let nowtime = new Date();
        let nowyear = nowtime.getFullYear();
        // console.log(item)
        if (item.gender === 1) {
          this.Hdetail[0]++;
        }
        if (item.gender === 0) {
          this.Hdetail[1]++;
        }
        if (parseInt(nowyear) - parseInt(year) < 18) {
          this.Hdetail[2]++;
        }
        if (parseInt(nowyear) - parseInt(year) > 18) {
          this.Hdetail[3]++;
        }
      });
      // 
      this.Sh.forEach(item => {
        let bir = new Date(item.birthday);
        let year = bir.getFullYear();
        let nowtime = new Date();
        let nowyear = nowtime.getFullYear();
        if (item.gender === 1) {
          this.Sdetail[0]++;
        }
        if (item.gender === 0) {
          this.Sdetail[1]++;
        }
        if (parseInt(nowyear) - parseInt(year) < 18) {
          this.Sdetail[2]++;
        }
        if (parseInt(nowyear) - parseInt(year) > 18) {
          this.Sdetail[3]++;
        }
      });
      // 
      this.Gd.forEach(item => {
        let bir = new Date(item.birthday);
        let year = bir.getFullYear();
        let nowtime = new Date();
        let nowyear = nowtime.getFullYear();
        if (item.gender === 1) {
          this.Gdetail[0]++;
        }
        if (item.gender === 0) {
          this.Gdetail[1]++;
        }
        if (parseInt(nowyear) - parseInt(year) < 18) {
          this.Gdetail[2]++;
        }
        if (parseInt(nowyear) - parseInt(year) > 18) {
          this.Gdetail[3]++;
        }
      });
      // 
      this.Other.forEach(item => {
        let bir = new Date(item.birthday);
        let year = bir.getFullYear();
        let nowtime = new Date();
        let nowyear = nowtime.getFullYear();
        if (item.gender === 1) {
          this.Odetail[0]++;
        }
        if (item.gender === 0) {
          this.Odetail[1]++;
        }
        if (parseInt(nowyear) - parseInt(year) < 18) {
          this.Odetail[2]++;
        }
        if (parseInt(nowyear) - parseInt(year) > 18) {
          this.Odetail[3]++;
        }
      });
    }
  },
  async mounted() {
    await this.getusers();
    var area = echarts.init(document.getElementById("user"));
    // 
    var option = {
      title: {
        text: "",
        left: "center"
      },
      tooltip: {
        trigger: "item",
        formatter: "{a} <br/>{b} : {c} ({d}%)"
      },
      legend: {
        orient: "vertical",
        left: "left",
        data: ["", "", "", "", ""]
      },
      series: [
        {
          name: "",
          type: "pie",
          radius: "55%",
          center: ["50%", "60%"],
          data: [],
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: "rgba(0, 0, 0, 0.5)"
            }
          }
        }
      ]
    };
    // 
      // console.log(this.Bj, this.Hn, this.Sh, this.Other);
      option.series[0].data.push(
        { value: this.Bj.length, name: "" },
        { value: this.Sh.length, name: "" },
        { value: this.Gd.length, name: "" },
        { value: this.Hn.length, name: "" },
        { value: this.Other.length, name: "" }
      );
      area.setOption(option);

    // 
    let detail = echarts.init(document.getElementById("detail"));
    let option2 = {
      title: {
        text: ""
      },
      tooltip: {
        trigger: "axis",
        axisPointer: {
          type: "cross",
          label: {
            backgroundColor: "#6a7985"
          }
        }
      },
      legend: {
        data: ["", "", "", ""]
      },

      grid: {
        left: "3%",
        right: "4%",
        bottom: "3%",
        containLabel: true
      },
      xAxis: [
        {
          type: "category",
          boundaryGap: false,
          data: ["","", "", "",  ""]
        }
      ],
      yAxis: [
        {
          type: "value"
        }
      ],
      series: [
        {
          name: "",
          type: "line",
          stack: "",
          areaStyle: {},
          data: []
        },
        {
          name: "",
          type: "line",
          stack: "",
          areaStyle: {},
          data: []
        },
        {
          name: "",
          type: "line",
          stack: "",
          areaStyle: {},
          data: []
        },
        {
          name: "",
          type: "line",
          stack: "",
          areaStyle: {},
          data: []
        }
      ]
    };
  
      // 
      // console.log(option2)
      option2.series[0].data.push(this.Bdetail[0],this.Hdetail[0],this.Sdetail[0],this.Gdetail[0],this.Odetail[0])
      option2.series[1].data.push(this.Bdetail[1],this.Hdetail[1],this.Sdetail[1],this.Gdetail[1],this.Odetail[1])
      option2.series[2].data.push(this.Bdetail[2],this.Hdetail[2],this.Sdetail[2],this.Gdetail[2],this.Odetail[2])
      option2.series[3].data.push(this.Bdetail[3],this.Hdetail[3],this.Sdetail[3],this.Gdetail[3],this.Odetail[3])
      detail.setOption(option2);

  }
};
</script>
<style scoped>
#user {
  width: 450px;
  height: 350px;
}
#gender {
  width: 200px;
  height: 150px;
}
.userreport {
  display: flex;
  justify-content: center;
}
#detail {
  width: 780px;
  height: 350px;
}
</style>